<template>
  <tr>
    <td>{{ userId }}</td>
    <td>{{ userPw }}</td>
    <td>{{ userEmail }}</td>
    <td>{{ userName }}</td>
    <td>{{ userPhone }}</td>
    <td>
      <a class="btn btn-primary btn-sm" @click="removeUser"></a>
    </td>
  </tr>
</template>

<script>
import { remove } from "@/api/user.js";

export default {
  name: "BoardListRow",
  props: {
    userId: String,
    userPw: String,
    userEmail: String,
    userName: String,
    userPhone: String,
    refresh: Function,
  },
  computed: {},
  methods: {
    removeUser() {
      alert(this.userId + " .");
      remove(this.userId);
      // ;
      this.$router.go();

      // this.$emit("this.refresh");
    },
  },
};
</script>

